import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import "./HalamanDetailGunung.css"; // Kita akan buat file CSS ini

// Data dummy yang lebih detail. Idealnya, ini didapat dari API.
const dummyGunungDetail = {
  1: {
    // ID ini harus cocok dengan ID dari halaman Eksplorasi
    name: "Gunung Gede",
    location: "Jawa Barat, Indonesia",
    elevation: 2958,
    difficulty: "Menengah",
    headerImageUrl:
      "https://images.unsplash.com/photo-1589182373726-e4f658ab50f0?q=80&w=2070&auto=format&fit=crop",
    description:
      "Gunung Gede adalah sebuah gunung api stratovolcano yang terletak di Pulau Jawa, Indonesia. Gunung Gede berada dalam ruang lingkup Taman Nasional Gede Pangrango, yang merupakan salah satu dari lima taman nasional yang pertama kali diumumkan di Indonesia pada tahun 1980. Gunung ini berada di wilayah tiga kabupaten yaitu Kabupaten Bogor, Cianjur dan Sukabumi, dengan ketinggian 2.958 m di atas permukaan laut.",
    trails: [
      { name: "Via Cibodas", difficulty: "Menengah", duration: "7-8 jam" },
      { name: "Via Gunung Putri", difficulty: "Menengah", duration: "6-7 jam" },
      { name: "Via Salabintana", difficulty: "Sulit", duration: "8-9 jam" },
    ],
    gallery: [
      "https://images.unsplash.com/photo-1618334437175-1823c4a2414e?q=80&w=1964&auto=format&fit=crop",
      "https://images.unsplash.com/photo-1596473536394-91a4574a4b49?q=80&w=1974&auto=format&fit=crop",
      "https://images.unsplash.com/photo-1593436854191-eee76a2698cf?q=80&w=1935&auto=format&fit=crop",
      "https://images.unsplash.com/photo-1627993096231-7e885c34a24a?q=80&w=2070&auto=format&fit=crop",
    ],
  },
  // Tambahkan data untuk gunung lainnya di sini dengan ID yang sesuai...
};

function HalamanDetailGunung() {
  const { id_gunung } = useParams(); // Mengambil ID dari URL
  const [gunung, setGunung] = useState(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    // Simulasi fetch data berdasarkan ID
    setIsLoading(true);
    setTimeout(() => {
      const data = dummyGunungDetail[id_gunung];
      setGunung(data);
      setIsLoading(false);
    }, 500);
  }, [id_gunung]);

  if (isLoading) {
    return <div className="loading-container">Memuat informasi gunung...</div>;
  }

  if (!gunung) {
    return (
      <div className="loading-container">Informasi gunung tidak ditemukan.</div>
    );
  }

  return (
    <div className="detail-gunung-page">
      <header
        className="gunung-header"
        style={{ backgroundImage: `url(${gunung.headerImageUrl})` }}
      >
        <div className="header-overlay">
          <h1>{gunung.name}</h1>
          <p>{gunung.location}</p>
        </div>
      </header>

      <main className="gunung-content-container">
        {/* Info Cepat */}
        <section className="info-cepat-panel">
          <div className="info-item">
            <i className="bi bi-geo-alt-fill"></i>
            <span>LOKASI</span>
            <p>{gunung.location}</p>
          </div>
          <div className="info-item">
            <i className="bi bi-graph-up"></i>
            <span>KETINGGIAN</span>
            <p>{gunung.elevation.toLocaleString()} mdpl</p>
          </div>
          <div className="info-item">
            <i className="bi bi-speedometer2"></i>
            <span>KESULITAN</span>
            <p>{gunung.difficulty}</p>
          </div>
        </section>

        {/* Deskripsi */}
        <section className="content-section">
          <h2>Tentang {gunung.name}</h2>
          <p className="gunung-description">{gunung.description}</p>
        </section>

        {/* Jalur Pendakian */}
        <section className="content-section">
          <h2>Jalur Pendakian</h2>
          <div className="jalur-list">
            {gunung.trails.map((jalur, index) => (
              <div key={index} className="jalur-card">
                <h3>{jalur.name}</h3>
                <p>
                  <strong>Estimasi:</strong> {jalur.duration}
                </p>
                <p>
                  <strong>Kesulitan:</strong> {jalur.difficulty}
                </p>
              </div>
            ))}
          </div>
        </section>

        {/* Galeri */}
        <section className="content-section">
          <h2>Galeri Foto</h2>
          <div className="galeri-grid">
            {gunung.gallery.map((url, index) => (
              <div key={index} className="galeri-item">
                <img src={url} alt={`Galeri ${gunung.name} ${index + 1}`} />
              </div>
            ))}
          </div>
        </section>
      </main>
    </div>
  );
}

export default HalamanDetailGunung;
